// Breadcrumbs ==================================

#breadcrumbs{
  // z-index in _sa-variables.css
  position: fixed;
  width: 100%;
}
.breadcrumb{
  border-radius: 0%;
  padding: $sa-spacer-4 $sa-spacer-16;
  background-color: white;
  border-bottom: 1px solid $sa-grey-04;
}

// Fixed Top Navbar =========
#breadcrumb-top{
  top: 0px;
  border-bottom: 1px solid $sa-grey-04;
  background-color: white;


  & .breadcrumb{
    padding: $sa-spacer-4 0;
    border-bottom: none;
    margin-bottom: 0;
  }
}

